.demo {
  min-height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #A4BDBA;
  max-width: 400px;
  min-width: 230px;
  padding: 15px;
  border-radius: 25px 25px 25px 25px;
  margin-bottom: 100px;
}

.button, .input, .checkbox, .checkboxWrapper {
  margin: 0;
  padding: 0;
  outline: none;
  position: relative;
}

// button ---------------------------------------------------

.button {
  cursor: pointer;
  padding: 5px 15px;
  border: 2px solid #73827f;
  border-radius: 3px;
  background: transparent;
  transition: 0.2s;
  transition-delay: 0.2s;
  z-index: 100;
  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,.4);
    z-index: -1;
    transform-origin: center;
    transform: scale(0);
    transition: 0.3s;
    transition-timing-function: ease-in-out;
  }
  &:hover {
    &:after {
      transform: scale(1);
    }
  }
  &:disabled {
    cursor: default;
    color: #666;
    &:after {
      transform: scaleX(0);
    }
  }
  &.typeRed {
    background: rgba(158, 4, 0, 0.6);
  }
}

// input ---------------------------------------------------

.input {
  border: none;
  padding: 5px 10px;
  background: #fff;
  border-top: 2px solid #fff;
  border-bottom: 2px solid#73827f;
  transition: .2s;
  &:focus {
    border-color: #0b9e00;
  }
  &.error {
    border-color: #9e0400;
  }
}

// checkbox ---------------------------------------------------

.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}
.checkbox + label {
  position: relative;
  padding: 0 0 0 60px;
  cursor: pointer;
}
.checkbox + label:before {
  content: '';
  position: absolute;
  top: -4px;
  left: 0;
  width: 45px;
  height: 20px;
  border-radius: 13px;
  background: #CDD1DA;
  box-shadow: inset 0 2px 3px rgba(0,0,0,.2);
  transition: .2s;
}
.checkbox + label:after {
  content: '';
  position: absolute;
  top: -2px;
  left: 4px;
  width: 15px;
  height: 15px;
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0 2px 5px rgba(0,0,0,.3);
  transition: .2s;
}
.checkbox:checked + label:before {
  background: #0b9e00;
}
.checkbox:checked + label:after {
  left: 26px;
}